import React from 'react'
import { addUsername, addPassword, resetCredentials } from '../reducers/loginReducer'
import { changeToneToNeg } from '../reducers/toneReducer'
import { addNotification, dropNotification } from '../reducers/notificationReducer'
import { loginUser } from '../reducers/currentUserReducer'
import { useDispatch, useSelector } from 'react-redux'
import blogService from '../services/blogs'

const LoginForm = () => {
  const dispatch = useDispatch()
  const username = useSelector(state => state.credentials.username)
  const password = useSelector(state => state.credentials.password)
  const user = useSelector(state => state.user)

  const handleLogin = async (event) => {
    event.preventDefault()
    console.log(`logging in with ${username} ${password}`)
    try {
      dispatch(loginUser({ username, password }))
      console.log('UUUUSERI')
      console.log(user)

      window.localStorage.setItem(
        'loggedBlogAppUser',
        JSON.stringify(user)
      )

      blogService.setToken(user.token)
      console.log('tokenin asetus onnistuu')
      console.log('kaikki onnistuu')
      dispatch(resetCredentials())
    } catch (exception) {
      console.log(exception)
      console.log('missing or invalid credentials')
      dispatch(changeToneToNeg())
      dispatch(addNotification('missing or invalid credentials'))
      setTimeout(()  => {
        dispatch(dropNotification(''))
      }, 5000)
    }
  }

  return (
    <div>
      <h2>Login to application</h2>
      <form id={'login-form'} onSubmit={handleLogin}>
        <div>
          username
          <input
            id='username'
            value={username}
            name="Username"
            onChange={({ target }) => dispatch(addUsername(target.value))}
          />
        </div>
        <div>
          password
          <input
            id='password'
            type="password"
            value={password}
            name="Username"
            onChange={({ target }) => dispatch(addPassword(target.value))}
          />
        </div>
        <button type="submit">login</button>
      </form>
    </div>
  )
}

export default LoginForm