import React, { useState } from 'react'


const Blog = ({ blog, changeBlog, removeBlog, loggedUsername }) => {
  const [visible, setVisible] = useState('')

  const showWhenVisible = { display: visible ? 'none' : '' }
  const hideWhenVisible = { display: visible ? '' : 'none' }

  const toggleVisibility = () => {
    setVisible(!visible)
  }

  const addLike = () => {
    //const changedBlog = { ...blog, likes: blog.likes + 1 }
    const changedBlog = {
      user: blog.user.id,
      likes: blog.likes + 1,
      author: blog.author,
      title: blog.title,
      url: blog.url,
    }
    changeBlog(changedBlog, blog.id)
    console.log(changedBlog)
  }

  const dropBlog = () => {
    window.confirm(`Remove blog "${blog.title}" by ${blog.author}?`)
      && removeBlog(blog.id)
  }

  const blogStyle = {
    paddingTop: 10,
    paddingLeft: 2,
    border: 'solid',
    borderWidth: 1,
    marginBottom: 5
  }

  console.log('Käyttäjät BLOGissa')
  console.log(blog)
  console.log(blog.user)
  console.log(blog.user.username)
  console.log(loggedUsername)
  const removeButtonStyle = {
    background: 'blue',
    color: 'white',
    display: blog.user.username === loggedUsername ? '' : 'none'
  }

  return (
    <li id='blog-info' style={blogStyle}>
      <table style={showWhenVisible} className='defaultView'>
        <tr>
          {blog.title} <button onClick={toggleVisibility}>view</button>
        </tr>
        <tr>
          {blog.author}
        </tr>
      </table>
      <table style={hideWhenVisible} className='allView'>
        <tr>
          {blog.title} <button onClick={toggleVisibility}>view</button>
        </tr>
        <tr>
          {blog.url}
        </tr>
        <tr>
          {blog.likes} <button id='like-button' onClick={addLike}>like</button>
        </tr>
        <tr>
          {blog.author}
        </tr>
        <tr>
          <button onClick={dropBlog} style={removeButtonStyle}>remove</button>
        </tr>
      </table>
    </li>
  )
}

export default Blog